<template>
    <div>
        <el-row :gutter="10" style="background-color:rgb(238, 238, 238);padding-top: 50px;padding-bottom: 50px;">
            <el-col :lg="18" :md="18" :sm="18" :xs="18" :offset="3" style="background-color:#fff;padding:30px 25px 15px 0">
                <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="200px" class="demo-ruleForm">
                    <el-form-item label="" prop="region">
                        <el-select v-model="ruleForm.region" placeholder="">
                            <el-option label="" value="shanghai"></el-option>
                            <el-option label="" value="beijing"></el-option>
                        </el-select>
                    </el-form-item>

                    <el-form-item label="" prop="designation">
                        <el-input v-model="ruleForm.designation"></el-input>
                    </el-form-item>

                    <el-form-item label="" prop="number">
                        <el-input v-model="ruleForm.number"></el-input>
                    </el-form-item>

                    <el-form-item label="" prop="resource">
                        <el-radio-group v-model="ruleForm.resource">
                            <el-radio label=""></el-radio>
                            <el-radio label=""></el-radio>
                            <el-radio label="2"></el-radio>
                            <el-radio label="2"></el-radio>
                            <el-radio label="3"></el-radio>
                        </el-radio-group>
                    </el-form-item>

                    <el-form-item label="" prop="manage">
                        <el-input v-model="ruleForm.manage"></el-input>
                    </el-form-item>

                    <el-form-item label="/" prop="need">
                        <el-input v-model="ruleForm.need"></el-input>
                    </el-form-item>

                    <el-form-item label="" prop="name">
                        <el-input v-model="ruleForm.name"></el-input>
                    </el-form-item>

                    <el-form-item label="" prop="phone">
                        <el-input v-model="ruleForm.phone"></el-input>
                    </el-form-item>

                    <el-form-item>
                        <el-button @click="submitForm('ruleForm')" style="background-color: #f48100;border:none;color:#fff;"></el-button>
                    </el-form-item>
                </el-form>
            </el-col>
        </el-row>
    </div>
</template>

<script type="text/ecmascript-6">
	export default {
		data() {
			return {
				ruleForm: {
					region: '',
					designation: '',
					number: '',
					resource: '',
					manage: '',
					need:'',
					name:'',
					phone: ''
				},
				rules: {
					region: [
						{ required: true, message: '', trigger: 'change' }
					],
					resource: [
						{ required: true, message: '', trigger: 'change' }
					],
					manage: [
						{ required: true, message: '', trigger: 'blur' }
					],
					name: [
						{ required: true, message: '', trigger: 'blur' }
					],
					phone: [
						{ required: true, message: '', trigger: 'blur' }
					]
				}
			};
		},
		methods: {
			submitForm(formName) {
				this.$refs[formName].validate((valid) => {
					if (valid) {
						alert('submit!');
					} else {
						console.log('error submit!!');
						return false;
					}
				});
			},
		}
	}
</script>

<style>

</style>


